




















































	export default {
		data() {
			return {
				advLists: [],
        orderLists: [],
        bankLists: []
			};
		},
		onLoad() {
			// banner
			this.$http({
				url: this.$apis.adverts,
				method: 'GET',
				data: {
					place: 3
				},
				cb: (err, data) => {
					if (!err && data.success) {
						this.advLists = data.data
					}
				}
			})
      // 最近申请
      this.$http({
        url: this.$apis.orderLatest,
        method: 'GET',
        data: null,
        cb: (err, data) => {
          if (!err && data.success) {
            this.orderLists = data.data
            this.orderLists.forEach(item => {
              item.create_time = this.$utils.formatTimes(item.create_time)
            })
          }
        }
      })
      // 银行列表
      this.$http({
      	url: this.$apis.banks,
      	method: 'GET',
      	data: null,
      	cb: (err, data) => {
      		if (!err && data.success) {
      			this.bankLists = data.data
      		}
      	}
      })
		},
    methods: {
      gotoContent(item) {
        let obj = {
          name: item.name,
          idcard: item.id_card,
          mobile: item.mobile
        }
        uni.setStorageSync('queryInfo', JSON.stringify(obj))
        this.$navigateTo({
        	url: './queryBank?id=' + item.apply_id + '&bankCode=' + item.bank_code
        })
      },
      gotoQueryBank(item) {
        if (item.isopen === 2) {
        	uni.showToast({
        		title: '该银行即将上线',
        		icon: 'none'
        	})
        	return
        }
        this.$navigateTo({
        	url: './queryBank?bankCode=' + item.code
        })
      },
      gotoOrder() {
        this.$navigateTo({
          url: '/pages/mine/order'
        })
      }
    }
	}
